let changelook = ['所有人可见', '尽自己可见']
import uniNavBar from "../../components/uni-nav-bar/uni-nav-bar.vue"
import uniIcons from "../../components/uni-icons/uni-icons.vue"
import uploadImages from "../../components/common/upload-images.vue"
import uniPopup from '@/components/uni-popup/uni-popup.vue'
export default {
	components:{
		uniNavBar,
		uniIcons,
		uploadImages,
		uniPopup
	},
	data() {
		return {
			yinsi:'所有人看见',
			text:'',
			imglist:[],
			showpopup:true,
			isget: false
		}
	},
	//监听页面返回
	onBackPress() {
		//如果有值
		if(!this.text && this.imglist.length < 1){
			return
		}
		if(!this.isget){
			this.baocun()
			return true
		}
	},
	methods: {
		//保存为草稿
		baocun(){
			uni.showModal({
				title: '提示',
				content: '是否保存为草稿',
				cancelText: '不保存',
				confirmText: '保存',
				success: (res) => {
					if (res.confirm) {
						console.log('保存');
					} else if (res.cancel) {
						console.log('不保存');
					}
					this.isget = true
					uni.navigateBack({
						delta: 1
					})
				}
			});
		},
		//返回
		back(){
			uni.navigateBack({
				delta: 1
			})
		},
		//发布
		submit(){
			console.log('发布')
		},
		changelook(){
			let _self = this
			uni.showActionSheet({
			    itemList: changelook,
			    success: (res) => {
					this.yinsi = changelook[res.tapIndex]
					console.log(this.yinsi)
			    }
			});
		},
		upload(arr){
			this.imglist = arr
		},
		hidePopup(){
			this.showpopup=false;
		}
	}
}
